@use 'base/mixins' as m;
@use 'base/variables' as v;

.boletos {
	&__grid {
		@include m.contenedor-sm;
		@include m.grid(1, 5rem);
	}
}

.boleto {
	width: min(100% - 5rem, 65rem);
	margin: 0 auto;
	padding: 6rem;
	border-radius: 2rem;
	color: v.$blanco;
	position: relative;
	text-align: center;

	&::before,
	&::after {
		position: absolute;
		content: '';
		background-color: v.$blanco;
		width: 8rem;
		height: 8rem;
		top: calc(50% - 4rem);
	}

	&::before {
		clip-path: circle(50% at 25% 50%);
		left: 0;
	}

	&::after {
		clip-path: circle(50% at 75% 50%);
		right: 0;
	}

	&--presencial {
		background-color:hsla(178,100%,39%,1);
		background-image:
		radial-gradient(at 55% 54%, hsla(209,100%,47%,1) 0px, transparent 50%),
		radial-gradient(at 92% 12%, hsla(178,100%,39%,1) 0px, transparent 50%),
		radial-gradient(at 19% 21%, hsla(209,100%,47%,1) 0px, transparent 50%),
		radial-gradient(at 30% 69%, hsla(178,100%,39%,1) 0px, transparent 50%),
		radial-gradient(at 87% 83%, hsla(209,100%,47%,1) 0px, transparent 50%),
		radial-gradient(at 13% 84%, hsla(178,100%,39%,1) 0px, transparent 50%);
	}
	&--virtual {
		background-color: v.$secundario;
	}
	&--gratis {
		background-color: v.$azul;
	}
	&__logo {
		@include m.logo;
		@include m.texto-fluido(2rem, 4rem)
	}
	&__plan {
		font-size: 2rem;
		text-transform: uppercase;
		margin: 3rem 0 0 0;
	}
	&__precio {
		margin: 3rem 0 0 0;
		font-weight: v.$black;
		font-size: 4rem;
	}
	&__enlace-contenedor {
		margin-top: 5rem; 
		display: flex;
		justify-content: center;
	}
	&__enlace {
		@include m.boton(v.$azul, v.$blanco);
		width: auto;
	}

	$modificador: &;
	&--acceso {
		width: 35rem;
		height: 50rem;
		padding: 10rem 3rem;
		
		@include m.tablet {
			display: flex;
			width: 60rem;
			height: 30rem;
			padding: 5rem 8rem;
			text-align: center;
			justify-content: center;
			align-items: center;
			
		}
		#{$modificador}__logo {
			@include m.texto-fluido(2rem, 3rem);
			margin: 0;
			text-align: center;
			margin-top: 2rem;
			@include m.tablet {
				margin-top: 0;
				text-align: left;
			}
		}
		
		#{$modificador}__codigo {
			font-size: 4rem;
			font-weight: v.$bold;
			text-align: center;
			@include m.tablet {
				rotate: 90deg;
			}
		}
	
		#{$modificador}__nombre {
			font-size: 2.4rem;
			text-transform: uppercase;
			font-weight: v.$black;
			text-align: center;
			@include m.tablet {
				text-align: left;
			}
		}

		#{$modificador}__plan {
			
			text-align: center;
			@include m.tablet {
				text-align: left;
			}
		}
	}
	&::before {
		clip-path: circle(50% at 50% 25%);
		left: calc(50% - 4rem);
		top: 0;
		@include m.tablet {
			clip-path: circle(50% at 25% 50%);
			left: 0;
			top: calc(50% - 4rem);
		}
	}

	&::after {
		clip-path: circle(50% at 50% 75%);
		left: calc(50% - 4rem);
		top: calc(100% - 8rem);
		@include m.tablet {
			clip-path: circle(50% at 75% 50%);
			left: calc(100% - 8rem);
			top: calc(50% - 4rem);
		}
	}
}